body {
  font-family: Helvetica, Arial, sans-serif;
}
section {
  margin: 120px 0;
}
h1,
h2 {
  font-weight: 900;
  line-height: 48px;
  margin: 0;
}
h1 {
  color: #1f05b5;
  text-align: center;
  font-size: 24px;
}
h2 {
  text-align: center;
  font-size: 24px;
  color: #885df1;
  padding: 0 0 20px;
}
h3 {
  text-align: center;
  font-size: 22px;
  color: #885df1;
  padding: 0 0 20px;
}
p {
  font-size: 18px;
  line-height: 28px;
  font-family: PT Mono, monospace;
  text-align: center;
}
button {
  border-radius: 25px;
  margin: 0 auto;
  display: block;
  background-color: #fff;
  border: 1px solid #885df1;
  color: #885df1;
  font-size: 16px;
  line-height: 22px;
  padding: 16px 24px;
  text-decoration: none;
  transition: all 200ms ease;
}
button:hover {
  color: #fff;
  background: #885df1;
}
.imgearth_ {
  margin: 0 auto;
  display: block;
  max-width: 100px;
  border-radius: 4px;
}
.imgearth {
  max-width: 200px;
  margin: 0 auto;
  display: block;
}

.gcoord {
  font-size: 20px;
  color: #1c035c;
}
.scoord {
  font-size: 20px;
  color: #36206e;
}

.text1 {
  font-size: 16px;
  margin: 20px auto;
}
.weath {
  text-align: center;
  text-decoration-color: #1f05b5;
}

#city,
#day_time {
  text-align: left;
}
#temper {
  text-align: left;
  color: rgb(33, 33, 33);
  font-size: 64px;
  font-weight: 400;
  line-height: 1;
}
